@mixin text($font-size: $H18, $font-weight: 500, $font-color: $WHITE) {
    font-size: $font-size;
    font-weight: $font-weight;
    color: $font-color;
}

@mixin flex ($jc: flex-start, $ai: stretch, $direction: row, $wrap: nowrap){
    display: flex;
    flex-direction: $direction;
    justify-content: $jc;
    align-items: $ai;
    flex-wrap: $wrap;
}
@mixin flex-col ($jc: flex-start, $ai: stretch, $direction: column, $wrap: nowrap){
    display: flex;
    flex-direction: $direction;
    justify-content: $jc;
    align-items: $ai;
    flex-wrap: $wrap;
}
@mixin size ($size) {
    weight: $size;
    height: $size;
}
